@import 'vars';
@import 'fonts';
@import 'mixins';


::placeholder {
  color: #666;
}
  
::selection {
  background-color: $accent;
  color: #fff;
}
  
html { 
  overflow-x: hidden;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, ::before, ::after {
  box-sizing: inherit;
}
  
body{
  position: relative;
  min-width: 320px;
  overflow: hidden;
  font-weight: 400;
  font-size: 16px;
  font-family: $font-default;
  line-height: 1.4;
  color: $text;
  
  input, textarea {
    border: #666 1px solid;
    outline: none;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

b, strong {
  font-weight: bold;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
   
h1, h2, h3, h4 {
  margin-top: 0;
  font-weight: 700;
  line-height: 1.2;
}

p {
  margin-top: 0;
}
  
.t-nowrap {
  white-space: nowrap;
}

@import 'animation';
  
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;

  @include desktop {
    max-width: 960px;
  }

  @include laptop {
    max-width: 760px;
    padding: 0 10px;
  }

  @include tablets {
    max-width: 540px;
  }

  @include phones {
    max-width: 320px;
  }
}

/* header */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;

  &__line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    background-color: $accent;
  }

  &__logo {
    position: relative;
    z-index: 10;
    align-self: stretch;
    width: 14vh;
    min-width: 120px;
    background-color: $dark;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  &__block {

    &_name {
      position: relative;
      z-index: 10;
      margin-right: auto;
      margin-left: 20px;
    }

    &_links {
      position: relative;
      z-index: 10;
      margin-left: auto;
      margin-right: 20px;
    }

    &_contacts {
      position: relative;
      align-self: stretch;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 14vh;
      min-width: 120px;
      font-size: 20px;
      background-color: $dark;
    }
  }

  &__toggle {
    display: none;
  }

  @include desktop {
    &__logo, &__block_contacts {
      width: 10vh;
      min-width: 80px;
    }

    &__block {

      &_name {
        margin-left: 10px;
        margin-right: 0;
      }
  
      &_links {
        margin-left: 0;
        margin-right: 10px;
      }
    }
  }

  @include laptop {
    &__toggle {
      position: relative;
      display: block;
    }

    &__line {
      min-height: 90px;
    }
  }

  @include phones {
    &__wrap {
      flex-direction: column;
    }

    &__line {
      height: 15vh;
    }

    &__logo, &__block_contacts {
      width: 10vh;
      min-width: 60px;
    }

    &__block {

      &_name {
        margin-left: 0;
      }
  
      &_links {
        margin-right: 0;
      }
    }
  }
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;

  &__wrap {
    
  }

  &__image {
    width: 40px;
    height: 40px;
  }
}

.logo-name {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 200px;
  line-height: 1;
  color: $white;

  &__top {
    font-weight: 700;
    font-size: 20px;
  }

  &__bottom {
    font-size: 15px;
  }

  @include desktop {
    width: 180px;

    &__top {
      font-size: 18px;
    }
  
    &__bottom {
      font-size: 13px;
    }
  }

  @include phones {
    margin-bottom: 10px;
  }
}

.menu {

  &__list {
    display: flex;
    @include lst;
  }

  &__link {
    padding: 0 25px;
    font-weight: 700;
    font-size: 20px;
    color: $white;
    white-space: nowrap;
    text-decoration: none;
    transition: $slowdown;

    &_current {
      color: $dark;
    }

    &:hover {
      color: gray;
    }

    &:active {
      color: $dark;
    }
  }

  @include hd {
    &__link {
      padding: 0 15px;
      font-size: 16px;
    }
  }

  @include desktop {
    &__link {
      padding: 0 7px;
      font-size: 15px;
    }
  }

  @include laptop {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 80vh;
    padding: 40px 0;
    background-color: $accent;
    transform: translateY(100%);

    &_show {
      display: flex;
      align-items: center;
      justify-content: center;
      transform: translateY(0);
      transition: transform 0.3s ease;
    }

    &__list {
      flex-direction: column;
      align-items: center;
    }

    &__item {
      padding: 15px 0;
    }

    &__link {
      font-size: 26px;
    }
  }
}

.links {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.2;

  &__phone {
    font-weight: 700;
    font-size: 18px;
    color: $white;
    text-decoration: none;
    white-space: nowrap;
    transition: $slowdown;

    &:hover {
      color: gray;
    }
  }

  @include hd {
    &__phone {
      font-size: 16px;
    }
  }
}

.link-cont {
  text-decoration: none;
  color: $white;
  transition: $slowdown;

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__img {
    width: 25px;
    height: 25px;
    margin-bottom: 5px;
  }

  &__text {
    font-weight: 700;
    font-size: 16px;
    text-decoration: none;
  }

  &:hover {
    color: darken($white, 20%) ;
  }

  @include desktop {
    &__text {
      font-size: 14px;
    }
  }

  @include laptop {
    display: none;
  }
}


.toggle__menu {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  display: none;
  width: 25px;
  height: 25px;
  @include no-btn;
  padding: 10px;
  transform: translate(-50%, -50%);

  span {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &::before, &::after {
      content: "";
      position: absolute;
      right: 0;
    }

    &::before {
      top: -8px;
    }
    &::after {
      top: 8px;
    }
  }

  span, span::before, span::after{
    width: 25px;
    height: 2px;
    background-color: $white;
    transition: $slowdown;
  }

  &_open {
    span, span::before, span::after{
      width: 10px;
    }
    
  }

  @include laptop {
    display: block;
  }
}

/* aside */
.aside {
  align-self: stretch;
  width: 20vh;
  min-width: 120px;
  max-width: 120px;
  background-color: $accent;
  
  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  &__inner {
    position: relative;
    padding-left: 200px;
    transform: rotate(-90deg);

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      height: 1px;
      width: 150px;
      background-color: $dark;
    }
  }

  &__text {
    margin-bottom: 0;
    font-weight: 500;
    color: $dark;
    text-align: center;
    white-space: nowrap;
  }

  @include desktop {
    width: 10vh;
    min-width: 80px;
  }

  @include phones {
    width: 10vh;
    min-width: 60px;
  }
}

/* cards */
.cards {
  display: flex;
  min-height: 100vh;
  

  &__aside {
    padding-top: 100px;
  }

  &__wrap {
    margin: 0 auto;
    padding-top: 100px;
    color: #333333;
  }

  &__inner {
    padding: 0 3vh;
    text-align: center;
  }

  &__title {
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 56px;
    color: #333333;
  }
  &__uptitle {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 24px;
  }
  &__subtitle {
    margin-bottom: 80px;
    font-weight: 700;
    font-size: 32px;
    line-height: 1.2;
  }

  &__lst {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @include lst;
    margin-bottom: 80px;
  }

  &__item {
    width: 50%;
    padding: 3vh;
  }

  &__link {
    width: 100%;
    height: 100%;
    text-decoration: none;

    &:hover {
      &::after {
        width: 100%;
        height: 2px;
      }
  
      &::before {
        width: 2px;
        height: 100%;
      }

      .cards {
        &__image {

          &::before {
            background-color: rgba(#000000, 0.8);
            opacity: 1;
          }

          img {
            transform: scale(1.1);
            filter: grayscale(1);
          }
        }

        &__logo {
          svg {
            opacity: 0;
          }
        }

        &__description {
          opacity: 1;
        }

        &__name {
          color: lighten($accent, 15%);
        }
      }
    }
  }

  &__image {
    position: relative;
    height: 400px;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-color: rgba(#000000, 0.8);
      transition: $slowdown;
      opacity: 0;
    }

    img { 
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
      transition: all 0.8s linear;
    }
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &_web {
      width: 250px;
    }

    &_mebel {
      width: 160px;
    }

    svg { 
      width: 100%;
    }
  }

  &__description {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin-bottom: 0;
    padding: 60px;
    font-weight: 500;
    font-size: 16px;
    color: $white;
    text-align: center;
    transition: $slowdown;
    opacity: 0;
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 130px;
    margin: -20px 5vh 0;
    padding: 25px;
    color: $white;
    text-align: center;
    background-color: #908b8b;
  }

  &__name {
    margin-bottom: 0;
    font-weight: 400;
    font-size: 26px;
    transition: $slowdown;
  }

  @include hd {
    &__image {
      height: 300px;
    }

    &__name {
      font-size: 22px;
    }
  }

  @include desktop {

    &__wrap {
      width: 100%;
    }

    &__title {
      font-size: 48px;
    }

    &__lst {
      flex-direction: column;
      align-items: center;
      width: 80%;
      margin: 0 auto;
      margin-bottom: 50px;
    }

    &__item {
      width: 100%;
      padding: 0;
      padding-bottom: 4vh;
    }

    &__name {
      font-size: 22px;
    }
  }

  @include laptop {
    &__wrap {
      width: 600px;
      margin: 0 auto;
    }

    &__inner {
      padding: 0 1vh;
    }
    &__image {
      height: 350px;
      &::before {
        opacity: 1;
      }
    }

    &__logo {
      top: 20px;
      left: 50%;
      z-index: 2;
      transform: translate(-50%, 0) scale(0.5);
      transform-origin: center top;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        height: 0;
        transform: translate(-50%, -50%);
      }

      svg {
        fill: $white;
      }
  
      img { 
        position: relative;
        min-width: auto;
      }
    }

    &__link {
      &:hover {
        .cards {
          &__logo {
            img {
              opacity: 1;
            }
          }
        }
      }
    }

    &__content {
      min-height: 110px;
      padding: 20px;
    }
    
    &__description {
      padding-top: 90px;
      padding-bottom: 30px;
      opacity: 1;
    }

    &__name {
      font-size: 18px;
    }

  }

  @include tablets {
    &__title {
      font-size: 38px;
    }

    &__uptitle {
      font-size: 20px;
    }

    &__subtitle {
      font-size: 24px;
    }
  }

  @include phones {

    &__wrap {
      padding-top: 120px;
    }

    &__title {
      margin-top: 20px;
      font-size: 32px;
    }

    &__subtitle {
      font-size: 20px;
    }

    &__lst {
      width: 100%;
    }

    &__image {
      height: 360px;
    }

    &__description {
      padding: 20px;
      padding-top: 90px;
      font-size: 14px;
      opacity: 1;
    }

    &__content {
      min-height: 90px;
      padding: 15px;
    }

    &__name {
      font-size: 16px;
    }
  }  
}

.footer {
  background-color: $dark;

  &__line {
    padding: 50px 30px;
    color: $white;
  }

  &__info {
    margin-bottom: 0;
    font-weight: 500;
    font-size: 22px;
    text-align: center;
  }

  @include laptop {
    &__info {
      font-size: 18px;
    }
  }
}